<template>
  <div class="block">
    <etc-cell-group>
      <etc-cell-item>
        <span slot="left">:</span>
        <input slot="right" type="number" placeholder="" />
      </etc-cell-item>
      <etc-cell-item arrow>
        <span slot="left"></span>
      </etc-cell-item>
      <etc-cell-item>
        <span slot="left"></span>
        <span slot="right"></span>
      </etc-cell-item>
    </etc-cell-group>
    <etc-cell-group>
      <etc-cell-item>
        <svg slot="icon" class="icon" aria-hidden="true"><use xlink:href="#icon-phone"></use></svg>
        <input slot="left" type="number" placeholder="" />
      </etc-cell-item>
      <etc-cell-item>
        <svg slot="icon" class="icon" aria-hidden="true"><use xlink:href="#icon-code"></use></svg>
        <input slot="left" type="number" placeholder="" />
        <div slot="right" class="btn-box">
          <etc-button type="primary" size="md"></etc-button>
        </div>
      </etc-cell-item>
      <etc-cell-item arrow type="a" href="tel:13719477521">
        <svg slot="icon" class="icon" aria-hidden="true"><use xlink:href="#icon-tel"></use></svg>
        <span slot="left"></span>
        <span slot="right">13719477521</span>
      </etc-cell-item>
      <etc-cell-item arrow type="link" href="#">
        <svg slot="icon" class="icon" aria-hidden="true"><use xlink:href="#icon-location"></use></svg>
        <span slot="left"></span>
        <span slot="right"></span>
      </etc-cell-item>
      <etc-cell-item arrow type="a">
        <img slot="icon" class="icon" src="../assets/logo.png" />
        <span slot="left"></span>
        <span slot="right"></span>
      </etc-cell-item>
    </etc-cell-group>
  </div>
</template>

<script>
  export default {
    methods: {
      test() {
        alert("666")
      }
    }
  }
</script>

<style lang="scss">
  .btn-box {
    width: 6rem;
  }
</style>



