<template>
  <div class="alert-box">
    <etc-button type="default" @click.native="comfirmClick" class="mt">comfirm</etc-button>
    <etc-button type="default" @click.native="AlertClick" class="mt">alert</etc-button>
  </div>
</template>

<script>
  export default {
    methods: {
      comfirmClick() {
        this.$dialog.alert({
          type: "comfirm",
          title: '',
          content: "?",
          // okTxt: "",
          // cancelTxt: "",
          // okColor: "#123",
          // cancelColor: "#234",
          ok: () => {
            this.$dialog.toast("");
          },
          cancel: () => {
            this.$dialog.toast("");
          }
        })
      },
      AlertClick() {
        this.$dialog.alert("~")
      }
    }
  }
</script>

<style lang="scss">

  .alert-box {
    padding: 1rem;
    height: 100%;
    background: #fff;
  }
  .mt {
    margin-top: 1rem;
  }
</style>



