<template>
  <div id="mescroll" class="mescroll">
    <slot name="list"></slot>
    <slot name="empty" v-if="is_show_empty && $slots.empty"></slot>
    <img ref="topSrc" v-if="toTop" style="display:none" src="../../../assets/images/icon-totop.png">
  </div>
</template>

<script type="text/babel">
  import MeScroll from '../../../plug-in/mescroll'
  export default {
    name: 'etc-vscroll',
    data() {
      return {
        mescroll: null,
        is_show_empty: false
      }
    },
    props: {
      onInfinite: {
        type: Function
      },
      onRefresh: {
        type: Function
      },
      pageSize: {
        type: [Number, String],
        defalut: 10
      },
      toTop: {
        type: Boolean,
        defalut: false
      }
    },
    mounted() {
      //MeScroll,down,,
      //: mescroll.resetUpScroll(); resetUpScrollpage.num=1,up.callback,;
      this.mescroll = new MeScroll("mescroll", {
        down: {
          use: true,
          auto: true,
          callback: this.downCallback,
        },
        up: {
          callback: this.upCallback, //
          //,
          page: {
            size: this.pageSize
          }, //8,10
          toTop: { //
            src: this.$refs.topSrc ? this.$refs.topSrc.src : null, //1000px,offset
            //offset : 1000
          },
        }
      });
    },
    methods: {
      refresh(property){
        this.$parent[property]=[];
        this.mescroll.resetUpScroll();
      },
      downCallback(mescroll) {
        mescroll.resetUpScroll();
      },
      // page = {num:1, size:10}; num: ,1; size:,10
      upCallback(page) {
        //
        this.onInfinite(page.num, page.size, data => {
          //,;
          //:; mescroll,;
          if (data.length == 0) {
            this.is_show_empty = true;
          }
          this.mescroll.endSuccess(data.length);
        }, () => {
          //,;
          this.mescroll.endErr();
        });
      }
    }
  }
</script>

<style lang="scss">
  @import '../../../style/necessary.scss';
  @import './vscroll.scss';
</style>



