<template>
  <div>
    <div class="group-title"></div>
    <div class="radio-group-box">
      <etc-radio-group v-model="checkboxGroupTest0">
        <etc-radio></etc-radio>
        <etc-radio></etc-radio>
        <etc-radio></etc-radio>
        <etc-radio></etc-radio>
      </etc-radio-group>
      <div class="radio-state">{{checkboxGroupTest0}}</div>
    </div>
    <div class="group-title">id,id</div>
    <div class="radio-group-box">
      <etc-radio-group v-model="checkboxGroupTest1">
        <etc-radio id="1"></etc-radio>
        <etc-radio id="2"></etc-radio>
        <etc-radio id="3"></etc-radio>
        <etc-radio id="4"></etc-radio>
      </etc-radio-group>
      <div class="radio-state">{{checkboxGroupTest1}}</div>
    </div>
    <div class="group-title"></div>
    <div class="radio-group-box">
      <etc-radio-group v-model="checkboxGroupTest3" type="checkbox">
        <etc-radio></etc-radio>
        <etc-radio></etc-radio>
        <etc-radio></etc-radio>
        <etc-radio></etc-radio>
      </etc-radio-group>
      <div class="radio-state">{{checkboxGroupTest3}}</div>
    </div>
    <div class="group-title"></div>
    <div class="radio-group-box">
      <etc-radio-group v-model="checkboxGroupTest2" color="#26a2ff">
        <etc-radio></etc-radio>
        <etc-radio></etc-radio>
        <etc-radio></etc-radio>
        <etc-radio></etc-radio>
      </etc-radio-group>
      <div class="radio-state">{{checkboxGroupTest2}}</div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        checkboxGroupTest0: '',
        checkboxGroupTest1: '2',
        checkboxGroupTest2: '',
        checkboxGroupTest3: '',
      }
    },
    mounted() {}
  }
</script>

<style lang="scss">
  .radio-group-box {
    background: #ffffff;
    padding: .5rem 0rem;
    .etc-radio-group {
      display: flex;
      justify-content: space-around;
    }
    .radio-state {
      padding: 1rem 1rem 0rem 1rem;
      font-size: .7rem;
      color: #000;
    }
  }
</style>



